import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
const Layout = React.lazy(() => import('./pages/Layout/Layout'));
const Community = React.lazy(() => import('./pages/Protected/Community/Community'));
const Home = React.lazy(() => import('./pages/Home/Home'));
const Profile = React.lazy(() => import('./pages/Protected/Profile/Profile'));
const SignUp = React.lazy(() => import('./pages/Sign-Up/SignUp'));
const Login = React.lazy(() => import('./pages/Login/Login'));
const Store = React.lazy(() => import('./pages/Protected/Store/Store'));
const NewGame = React.lazy(() => import('./pages/Protected/NewGame/NewGame'));

function App() {
  return (
    <>
        <Router>
          <Routes>
            <Route path='/store.parrotcloud/' element={<Layout />}>
              <Route index element={<Home />} />
              <Route path='/store.parrotcloud/store' element={<Store />} />
              <Route path='/store.parrotcloud/community' element={<Community />} />
              <Route path='/store.parrotcloud/new-game' element={<NewGame />} />
              <Route path='/store.parrotcloud/login' element={<Login />} />
              <Route path='/store.parrotcloud/profile' element={<Profile />}/>
              <Route path='/store.parrotcloud/sign-up' element={<SignUp />} />
            </Route>
          </Routes>
        </Router>
    </>
  );
}

export default App;
